<template>
  <div class="payments">
    <section>
      <request
        v-for="request in requests"
        :key="request"
        :requestId="request._id"
        requestType="payments"
      >
        {{
          `username: ${request.username}
number: ${request.number}
status: ${request.status}
provider: ${request.provider}
number type: ${request.numType}
amount: ${request.amount}
requested in: ${request.createdAt}`
        }}
      </request>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
import request from "../components/request.vue";
export default {
  name: "Payments",
  data() {
    return {
      requests: [],
    };
  },
  components: { request },
  async mounted() {
    let rs = await axios.get(`${this.$store.state.server}/payments`);
    if (rs.status === 200) {
      this.requests = rs.data;
    } else {
      alert(rs.data);
    }
  },
};
</script>

<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
}

@media (min-width: 800px) {
  section {
    grid-template-columns: 1fr 1fr;
  }
}
</style>
